import { lazy, Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import Layout from '../components/ui/Layout/Layout';

import { QueryClient, QueryClientProvider } from '@tanstack/react-query';

import './App.css';
import { ErrorBoundary } from '../components/ui/Layout/ErrorBoundary/ErrorBoundary';

const NotFound = lazy(() => import('../templates/NotFound/NotFound'));
const Provision = lazy(() => import('../templates/Provision/Provision'));
const ProvisionTest = lazy(() => import('../templates/ProvisionTest/ProvisionTest'));
const ProvisionTestCustomDomain = lazy(() => import('../templates/ProvisionTest/ProvisionTestCustomDomain'));

const queryClient = new QueryClient();

function App() {
  return (
    <HelmetProvider>
      <Helmet>
        <meta name="v" content={process.env.REACT_APP_VERSION} />
      </Helmet>
      <QueryClientProvider client={queryClient}>
        <Router>
          <Suspense fallback={<Layout />}>
            <Routes>
              <Route
                path="/"
                element={
                  <Layout>
                    <Suspense>
                      <ErrorBoundary>
                        <Provision />
                      </ErrorBoundary>
                    </Suspense>
                  </Layout>
                }
              />
              <Route
                path="/test"
                element={
                  <Layout>
                    <Suspense>
                      <ErrorBoundary>
                        <ProvisionTest />
                      </ErrorBoundary>
                    </Suspense>
                  </Layout>
                }
              />
              <Route
                path="/testcustomdomain"
                element={
                  <Layout>
                    <Suspense>
                      <ErrorBoundary>
                        <ProvisionTestCustomDomain />
                      </ErrorBoundary>
                    </Suspense>
                  </Layout>
                }
              />
              <Route
                path="*"
                element={
                  <Layout>
                    <NotFound />
                  </Layout>
                }
              />
            </Routes>
          </Suspense>
        </Router>
      </QueryClientProvider>
    </HelmetProvider>
  );
}

export default App;
